import { dataFormat } from '../../../frame_template/js/utils.js'
import zPopup from '../module/Popup'
export default {
  components: {
    zPopup,
  },

  data() {
    return {
      tableData: [],
      projectId:localStorage.getItem('kanbanprojectId'),
      dialogShow: false,
      addDateValue: '',
      tagsTime: [],
    }
  },

  mounted() {

  },

  methods: {
    showDate () {
      let _this = this;
      this.$vux.datetime.show({
        cancelText: '取消',
        confirmText: '确定',
        format: 'YYYY-MM',
        value: _this.addDateValue || dataFormat(new Date(), 'YYYY-MM'),
        onShow () {
        },
        onHide () {
          _this.addDateValue = this.value
        }
      })
    },

    addTime() {
      if( this.addDateValue === '' ) return;

      this.tagsTime.push(this.addDateValue)
    },

    handleClose(index) {
      this.tagsTime.splice(index, 1);
    },

    save() {
//    this.$vux.loading.show({
//      transition: '',
//      text: '请稍后...'
//    })
//    setTimeout(() => {
//      this.queryClick();
//      this.$vux.loading.hide();
//      reslove();
//    }, 1000)
				this.queryClick();
				this.dialogShow=false;
    },
    queryClick(){
    	var monthstr = '';
    		if(this.tagsTime.length == 0) {
					this.$message({
						showClose: true,
						type: 'error',
						message: '请选择月份!'
					});
					return false;
				}
    	  this.tagsTime.forEach((item, index) => {
					monthstr+=item+',';
				})
      this.queryCountInit(monthstr.substring(0,monthstr.length-1));
    },
    queryCountInit(monthstr){
//				var temp = this;
				this.$post('/web/monthContrastCount', {"projectId":this.projectId,"months":monthstr}).then((m)=> {
					this.tableData=m.content.list;
				})
			},
  }
}



